.app-logo {
    background: url('../images/logo-app.svg') no-repeat center center;
}

.l-splash-holder {
    background: #333;
    .s-splash {
        border-radius: 10px;
        box-shadow: 0 5px 50px 25px rgba(255, 255, 255, 0.1);
    }
}

.s-splash {
    background-image: url('../images/bg-splash.jpg');
    &:after {
        background-image: url('../images/logo-app-shdw.svg');
    }
}
